import React, { Fragment,useEffect } from "react";
import SEO from "../../components/seo";
import LayoutOne from "../../layouts/LayoutOne";
import HeroSliderSixteen from "../../wrappers/hero-slider/HeroSliderSixteen";
import CategoryThreeSlider from "../../wrappers/category/CategoryThreeSlider";
import TabProductTen from "../../wrappers/product/TabProductTen";
import CategoryFiveGrid from "../../wrappers/category/CategoryFiveGrid";
import Loading from "../../components/Loading/Loading";
import { useDispatch,useSelector } from "react-redux";
import { HomeMainAction, HomePageProductstAction } from "../../store/slices/HomePageSlice/HomePageSlice";
const HomeElectronicsTwo = () => {
  const HomeReducer = useSelector((state) => state.HomeReducer)
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(HomePageProductstAction())
    dispatch(HomeMainAction())
  }, [])
  
  console.log(HomeReducer?.HomeMainRes,"Line 17")
  
  return (
    <Fragment>
      <SEO
        titleTemplate="iAdvance | Apple"
        description="Electronics home of flone react minimalist eCommerce template."
      />
      <LayoutOne
        headerContainerClass="container-fluid"
        headerPaddingClass="header-padding-1"
      >
        <HeroSliderSixteen data={HomeReducer?.HomeMainRes?.sliders} />
        <h2 style={{ marginTop: "50px", color: "#6364bf", textAlign: "center", fontWeight: "600", fontSize: "33px" }}>Explore Categories</h2>
        <CategoryThreeSlider spaceTopClass="pt-20" data={HomeReducer?.HomeMainRes?.explore_categories} category="1" />
        <h2 style={{ marginTop: "50px", color: "#6364bf", textAlign: "center", fontWeight: "600", fontSize: "33px" }}>Sale Items</h2>
        <TabProductTen
          spaceBottomClass="pb-50"
          spaceTopClass="pt-50"
          category="electronics"
          data={HomeReducer?.HomeProductsRes?.sale_products
          }
        /> 
        <h2 style={{ marginTop: "50px", color: "#6364bf", textAlign: "center", fontWeight: "600", fontSize: "33px" }}>Best Sellers</h2>
        <TabProductTen
          spaceBottomClass="pb-50"
          spaceTopClass="pt-50"
          category="electronics"
          data={HomeReducer?.HomeProductsRes?.best_seller_products}
        /> 
        <h2 style={{ marginTop: "50px", color: "#6364bf", textAlign: "center", fontWeight: "600", fontSize: "33px" }}>New Arrivals</h2>
       <TabProductTen
          spaceBottomClass="pb-50"
          spaceTopClass="pt-50"
          category="electronics"
          data={HomeReducer?.HomeProductsRes?.new_arrival_products}
        /> 
        {/* <CategoryFiveGrid spaceTopClass="pt-80" spaceBottomClass="pb-20" category="1" /> */}
        {/* <CategoryThreeSlider spaceTopClass="pt-50" spaceBottomClass="pb-95" category="2" /> */}
      </LayoutOne>
      {HomeReducer?.isLoading&&<Loading/>}
    </Fragment>
  );
};

export default HomeElectronicsTwo;
