import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import { IMAGE_BASE_URL } from "../../utils/constant";
const CategoryThreeSingle = ({ data }) => {
  return (
    <div className="collection-product">
      <div className="collection-img">
        <Link to={process.env.PUBLIC_URL + "/products" + "/" + data?.category_slug + "/" + data?.category_id}>
          <img src={data?.category_cover ? IMAGE_BASE_URL+ data?.category_cover : "https://www.iadvance.in/thumb_cache/350x350__width_height__1667977008__uploaded_files%5E2f0bf3697ff7f287eb67905cdfa47ceb.jpg"} alt="" />
        </Link>
      </div>
      <div className="collection-content text-center pb-30">
        <span>{data.product_count}</span>
        <h4>
          <Link to={process.env.PUBLIC_URL + "/products"}>{data.category_name}</Link>
        </h4>
        {/* <Link
          to={process.env.PUBLIC_URL + data.link}
          className="collection-btn"
        >
          SHOP NOW
        </Link> */}
      </div>
    </div>
  );
};

CategoryThreeSingle.propTypes = {
  data: PropTypes.shape({})
};

export default CategoryThreeSingle;
